import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { Carrerac } from '../models/carrera';
import { catchError, map } from 'rxjs/operators';
import Swal from 'sweetalert2';
import { Carrera, Estudiante } from '../../estudiante/models/estudiante';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CarreraService {
  url = `${environment.urlG}/carrera`

  constructor(
    private http: HttpClient
  ) { }

  public carreras(): Observable<Carrerac[]> {
    return this.http.get<Carrerac[]>(`${this.url}`)
  }
  findByName(nombrecarrera: string) {
    return this.http.get(`${this.url}/ifExist/${nombrecarrera}`)
  }
  public guardarCarrera(x: Carrerac) {
    return this.http.post(`${this.url}`, x)
    .pipe(
      catchError(e =>{
          Swal.fire('Error', 'Ya existe la carrera', 'error')
          return throwError(e);
      })
    )
  }
  public eliminar(id: number):Observable<Carrerac> {
    return this.http.delete<Carrerac>(`${this.url}/${id}`).pipe(
      catchError(e =>{
        Swal.fire('error', e.error.error)
        return throwError(e);
      })
    )
  }
  public editar(x: Carrerac) {
    return this.http.put(`${this.url}/${x.id}`, x)
  }
  public estudiantes(): Observable<Estudiante[]>{
    return this.http.get<Estudiante[]>(`${this.url}/estudiantes`)
  }
}
