import { Component, OnInit, ViewChild } from '@angular/core';
import Swal from 'sweetalert2';
import { Carrerac } from '../../models/carrera';
import { Estudiante } from 'src/app/pages/estudiante/models/estudiante';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { CarreraService } from '../../service/carrera.service';
import { isCaracter } from 'src/app/constantes/constantesproyecto';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';


@Component({
  selector: 'app-carrera',
  templateUrl: './carrera.component.html',
  styleUrls: ['./carrera.component.scss']
})
export class CarreraComponent implements OnInit {
  breadCrumbItems: Array<{}>;
  carreras: Carrerac[] = []
  estudiantes: Estudiante[]
  carrera: Carrerac
  formulario !: FormGroup
  showLoading = false;
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Carrerac> = new Subject<Carrerac>();

  constructor(
    private carreraService: CarreraService,
    private fb: FormBuilder,
    private modalService: NgbModal
  ) { this.formulario = this.inifC() }

  ngOnInit(): void {
    this.breadCrumbItems = [{ label: 'Listado' }];
    /**table */
    this.dtOptions = {
      columnDefs: [
        { orderable: false, targets: [0, 2] },
        { searchable: false, targets: [0, 2] },
        // { width: "50%", targets: [0, 1] },
        { responsivePriority: 1, targets: -1 }
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningún dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatos()
  }
  ngOnDestroy() { //para destruir la peticion
    this.dtTrigger.unsubscribe();
  }
  private inifC(): FormGroup {
    return this.fb.group({
      nombrecarrera: ['', [Validators.required, Validators.minLength(4), Validators.pattern(isCaracter)]]
    })
  }
  cargarDatos() {
    this.carreraService.carreras().subscribe((resp: Carrerac[]) => {
      this.carreras = resp;
      this.dtTrigger.next(null)
      this.carrera = null
    });
    this.carreraService.estudiantes().subscribe(resp => {
      this.estudiantes = resp
    })
  }
  esCampoValido(campo: string) {
    const validarCampo = this.formulario.get(campo)
    return !validarCampo?.valid && validarCampo?.touched
      ? 'is-invalid' : validarCampo?.touched ? 'is-valid' : '';
  }
  push() {
    if (this.carrera != null) {
      this.editar()
    } else {
      this.guardar()
    }
  }
  guardar() {
    this.showLoading = true
    this.carrera = new Carrerac(this.formulario.get('nombrecarrera').value)
    if (this.existe(this.carrera.nombrecarrera)) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        icon: "warning",
        title: "Advertencia",
        text: "Ya existe la carrera",
      });
      this.formulario.reset()
      this.carrera = null
    }
    else {
      this.carreraService.guardarCarrera(this.carrera).subscribe(resp => {
        if (resp) {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: 'Almacenado correctamente',
            icon: 'success'
          });
          this.reload()
          this.formulario.reset()
        } else {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: 'Ocurrio un problema',
            icon: 'warning'
          });
        }
      })
    }
    this.formulario.reset()
    return Object.values(this.formulario.controls)
      .forEach(control =>
        control.markAsTouched())

  }
  paraEdit(x: Carrerac) {
    this.formulario.reset()
    this.formulario.patchValue(x)
    this.carrera = x
  }
  editar() {
    this.showLoading = true
    const nuevoNombre = this.formulario.controls['nombrecarrera'].value;
    if (nuevoNombre === this.carrera.nombrecarrera) {
      this.showLoading = false
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Éxito',
        text: 'No se realizaron cambios.',
        icon: 'success',
      });
      this.formulario.reset()
        this.carrera = null
    }
    else {
      if (this.existe(nuevoNombre)) {
        this.showLoading = false
        Swal.fire({
          confirmButtonColor: "#a90000",
          icon: "warning",
          title: "Advertencia",
          text: "Ya existe la carrera",
        });
        this.formulario.reset()
        this.carrera = null
      } else {
        this.carrera.nombrecarrera = nuevoNombre
        this.carreraService.editar(this.carrera).subscribe(resp => {
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Éxito',
            text: 'Modificado correctamente',
            icon: 'success',
          });
          this.formulario.get('nombrecarrera').setValue("")
          this.reload()
        }, (err) => {
          this.reload()
          this.showLoading = false
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Error',
            text: `Algo fallo`,
            icon: 'error',
          });
        })
        this.formulario.reset()
      }
    }
  }
  eliminar(x: Carrerac) {
    Swal.fire({
      title: '¿Está seguro de eliminar? ',
      text: `Esto no se puede revertir`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: "#a90000",
      cancelButtonColor: '#343a40',
      confirmButtonText: 'Sí, borrar',
      cancelButtonText: 'Cancelar'
    }).then((result) => {
      if (result.isConfirmed) {
        const v = this.verificar(x.id)
        if (v) {
          Swal.fire({
            confirmButtonColor: "#a90000",
            title: 'Advertencia',
            text: `El registro no puede ser eliminado`,
            icon: 'warning',
          });
        } else {
          this.carreraService.eliminar(x.id).subscribe(response => {
            Swal.fire({
              confirmButtonColor: "#a90000",
              title: 'Eliminado',
              text: `El registro ha sido eliminado`,
              icon: 'success',
            });
            this.formulario.reset()
            this.reload()
          });
        }
      } else if (result.isDenied) {
        Swal.fire({
          confirmButtonColor: "#a90000",
          title: 'Información',
          text: `Cambios no aplicados`,
          icon: 'info',
        });
      }
    });
  }
  verificar(id: number): boolean {
    return this.estudiantes.some(estudiante => estudiante.carrera.id === id)
  }
  /**Para recargar */
  reload() {
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      // this.carreras = []
      dtInstance.destroy()
      this.cargarDatos()
    })
  }
  existe(nombre: string): boolean {
    const normalizedNombre = nombre.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
    return this.carreras.some(carrera => {
      const normalizedCarrera = carrera.nombrecarrera.normalize("NFD").replace(/[\u0300-\u036f]/g, "");
      return normalizedCarrera.toLocaleLowerCase() === normalizedNombre.toLocaleLowerCase();
    });
  }

  openModalA(content) {
    this.modalService.open(content, { keyboard: false, scrollable: true, size: 'lg' });
  }
}
